.footer{
    border-top: 1px solid $gray;
    padding-top: 77px;
    padding-bottom: 60px;
    .logo{
        width:179px ;
        height: 48px;
    }
}
.box-footer{
    display: flex;
    justify-content: space-between;
    &-item{
        display: flex;
        justify-content: space-between;
        flex: 0 0 66.4%;
        @media(max-width:1200px){
            flex: 0 0 72%;
        }
    }
    &-social{
        display: flex;
        justify-content: space-between;
        align-items: center;
        list-style-type: none;
        &__vk{
            background-image: url(../../../img/vk.svg);
            background-size: 25px 18px;
        }
        &__inst{
            background-image: url(../../../img/instagram.svg);
            background-size: 18px 18px;
        }
        &__fb{
            background-image: url(../../../img/facebook.svg);
            background-size: 23px 18px;
        }
        &__link{
            width: 48px;
            height: 48px;
            border: 2px solid $gray;
            border-radius: 100%;
            background-repeat: no-repeat;
            display: block;
            background-position:center center;
            opacity: 0.8;
            transition: 0.2s ease;
        }
    }
    &-list{
        font-size: 1.12rem;
        list-style-type: none;
        &__item{
            margin-bottom: 1.62rem;
        }
        &__tel{
            font-size: 1.68rem;
            margin-bottom: 2.06rem;
        }
        &__link{
            color: $gray;
            text-decoration: none;
        }
    }
}
.box-footer-social{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.box-copyright{
    display: flex;
    justify-content: space-between;
    margin-top: 42px;
    &__copy{
        opacity: 0.6;
    }
    &__text{
        color: #a6a8a4;
        position: relative;
    }
    &__link{
        text-decoration: none;
        color: $gray;
        opacity: 0.6;
        transition: 0.2s ease;
        &:hover{
            opacity: 1;
        }
    }
}