.main{
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media(max-width: 1200px){
        flex-direction: column;
    }
    &-text{
        flex: 0 0 40%;
        padding-top: 20px;
        @media(max-width: 1440px){
            flex: 0 0 45%;
        }
        @media(max-width: 1200px){
            text-align: center;
            margin-bottom: 100px;
        }
      &__description{
          font-size: 1.12rem;
          line-height: 1.51;
          margin-top: 60px;
          margin-bottom: 77px;
          @media(max-width: 1200px){
            max-width: 570px;
            margin-bottom: 45px;
        }
      } 
      &__image{
        @media(min-width: 1201px){
            display: none;
        }
        @media(max-width: 1200px){
            display: block;
            width: 427px;
            margin: 0 auto 65px;
        }
      }
    }
    &-visual{
        border: 1px solid rgba($gray, $alpha);
        border-radius: 100%;
        padding: 53px;
        position: relative;
        @media(max-width: 1200px){
            display: none;
         }
        &__image{
            border: 1px solid rgba($gray, $alpha);
            border-radius: 100%;
            padding: 21px;
            img{
                border-radius: 100%;
                box-shadow: 4px 48px 42px 0px rgba($gray, $alpha);
                @media(max-width: 1440px){
                    width: 425px;
                }
            }
        }
        &__btn{
            background-color: transparent;
            border: none;
            color: rgba($gray, 0.8);
            text-align: left; 
            position: absolute;
        
            span{
                color: rgba($gray, 1);
                display: block;
                margin-top: 6px;
            }
            &::before{
                content: '';
                display: block;
                width: 11px;
                height: 11px;
                border-radius: 100%;
                background-color: $gray;
                box-shadow: 0 0 0 4px #fff, 0 0 0 5px $gray;
                position: absolute;
            }
            &:focus{
                outline: none;
               &::before{
                background-color: $orange;
                box-shadow: 0 0 0 4px #fff, 0 0 0 5px $orange;
               } 
            }
        }
       &__01{
        left: 57px;
        top: 47px;
        @media(max-width: 1440px){
            left: 130px;
            top: 38px;
        }
        &::before{
            left: 90px;
            top: 23px;
        }
       }
       &__02{
        right: -3px;
        top: 194px;
        @media(max-width: 1440px){
            right: -37px;
            top: 200px;
        }
        &::before{
            right: 94px;
            top: 18px;
        }
      }
      &__03{
        right: 239px;
        bottom: -50px;
        @media(max-width: 1440px){
            right: 235px;
            bottom: -63px;
        }
        &::before{
            right: 36px;
            bottom: 56px;
        }
      }
    }
}
.section-stock{
    background-image:url(../img/bg-2.png) ;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding-bottom: 160px;
    &__text{
        font-size: 1.5rem;
        line-height: 1.5;
        max-width: 690px;
        margin-top: 3.75rem;
    }
}
.slider{
    margin-top: 92px;
         &-child{
        padding: 5px 10px;
        box-sizing: border-box;
            &:focus{
                outline: none;
             }  
        }
     &-item{
            border: 1px solid rgba($gray, $alpha);
            border-radius: 20px;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: center center;
            min-height: 520px;
            position: relative;
            padding: 2rem 2.25rem;
            box-sizing: border-box;
            box-shadow: 0 0 0 2px rgba($gray, 0); 
            transition: 0.3s ease;
            overflow: hidden;
            &:hover{
                box-shadow: 0 0 0 2px rgba($gray, $alpha); 
                }
            &:hover{
                .slider-text{
                 transform: translate(0, 0);
                }
            }
        }
        &-hover{
            padding: 2.75rem 2.25rem;
            transition: 0.3s ease;
            overflow: hidden;
            &::before{
                content: '';
                position: absolute;
                top: 0;
                right: 0;
                left: 0;
                bottom: 0;
                display: block;
                width: 100%;
                height: 100%;
                border-radius: 20px;
                transition: 0.3s ease;
            }
            &:hover::before{
                content: '';
                background-color:rgba($white,0.55);
            }
        }  
    &-milk{
        background-image: url(../img/stock/milk.png);
    }
    &-bakaleya{
        background-image: url(../img/stock/pasta.png);
    }
    &-meat{
        background-image: url(../img/stock/meat.png);
    }
    &-text{
        position: absolute;
        bottom: 2.75rem;
        left: 2.25rem;
        max-width: 58%;
        transform: translate(0, 245px);
        transition: 0.3s ease;
        @media(max-width:1440px){
            transform: translate(0, 255px);
            max-width: 80%;
        }
        @media(max-width:1200px){
            transform: translate(0, 205px);
        }
        &__title{
            font-size: 1.875rem;
            font-family: 'Cormorant', serif;
            margin-bottom: 26px;
        }
        &__description{
            color: rgba($gray, 0.7);
            margin-bottom: 45px;
            line-height: 1.5;
        }
        &__link{
            color:$gray;
            text-transform: uppercase;
            text-decoration: none;
            font-weight: 500;
            letter-spacing: 0.075rem;
            opacity: 0.8;
            position: relative;
            padding: 50px;
            transition: 0.3s ease;
            &:hover{
                opacity: 1;
                padding: 45px;
            }
            &:focus{
                outline: none;
            }
            &::before{
                content: '';
                position: absolute;
                left: 0;
                top: calc(50% - 0.93rem) ;
                display: block;
                width: 1.875rem;
                height: 1.875rem;
                background-image: url(../img/plus.svg);

            }
        }
    }
    &-thumb{
        position: relative;
        overflow: hidden;
        width: 100%;
        height: 314px;
        margin-bottom: 40px;
        &__image{
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
            object-fit: cover;
        }
    }
}
    .slider-info{
        &__title{
            font-size: 1.875rem;
            font-family:'Cormorant', serif;
            margin-bottom: 26px;
            @media(max-width: 1440px){
                min-height: 68px;
            }
        }
        &__description{
            font-size: 1.12rem;
            color: rgba($gray, 0.7);
            line-height: 1.5;
            margin-bottom: 0;
            min-height: 52px;
            max-width: 96%;
        }
        &__price{
            font-size: 1.5rem;
            margin-top: 28px;
        }
    }
    .slider-footer{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 32px;
        margin-bottom: 8px;
        &__btn{
            border: none;
            padding: 1.4rem 3.2rem;
            cursor: pointer;
            @media(max-width: 1440px){
                padding: 1rem 1.8rem;
            }

        }
    }
    .slider-count{
        display: flex;
        align-items: center;
        font-size: 1.5rem;
        &__minus,&__plus{
            display: block;
            width: 3.12rem;
            height: 3.12rem;
            background-repeat: no-repeat;
            background-size: cover;
            cursor: pointer;
            opacity: 0.4;
            transition: 0.2s ease;
            &:hover{
                opacity: 1;
            }
            @media(max-width: 1440px){
                width: 2.5rem;
                height: 2.5rem;
            }
        }
        &__number{
            border: none;
            width: 40px;
            text-align: center;
            @media(max-width: 1440px){
             width: 30px;   
            }
        }
        &__minus{
            background-image: url(../img/minus.svg);
        }
        &__plus{
            background-image: url(../img/plus.svg);
        }
    }
.slick-prev, .slick-next{
    position: absolute;
    background-color: transparent;
    border: none;
    top: -63px;
    text-indent: -9999px;
    background-repeat: no-repeat;
    background-size: contain;
    display: block;
    width: 10px;
    height: 18px;
    opacity: 0.4;
    transition: 0.2s ease;
    cursor: pointer;
    &:focus{
        outline:none;
    }
    &:hover{
        opacity: 1;
        &:focus{
            outline:none;
        }
    }
}
.slick-prev{
    right: 80px;
    background-image: url(../img/arrow-prev.svg);
}
.slick-next{
    right: 0;
    background-image: url(../img/arrow-next.svg);
}
.slick-dots{
    display: flex;
    list-style-type: none;
    justify-content: center;
    text-indent: -9999px;
    &>li{
        width: 10px;
        height: 10px;
        background: $gray;
        margin-right: 10px;
        margin-left: 10px;
        border-radius: 100%;
        margin-top: 30px;
    }
    &>li.slick-active{
        background: $orange;
    }
}

