    &__h3{
        font-family: 'Cormorant', serif;
        font-size: 1.87rem;
        margin-bottom: 30px;
    }
    &__label{
        display: block;
        font-size: 0.75rem;
        margin-bottom: 10px;
        margin-left: 5px;
    }
    &__input{
        display: block;
        width: 132%;
        box-sizing: border-box;
        border: 1px solid rgba($gray, $alpha);
        border-radius: 20px;
        padding: 1.4rem 1.9rem;
        margin-bottom: 29px;
        transition: 0.2s ease;
        &:focus{
            outline: none;
            border: 1px solid $gray;
        }
    }
    &__text{
        font-size: 0.75rem;
        opacity: 0.6;
        margin-top: 15px;
        line-height: 1.5;
    }
    &__link{
        color:$gray;
        border-bottom:1px solid rgba($gray, 0.6) ;
        text-decoration: none;
    }
    .btn{
        margin-top: 11px;
        padding: 1.5rem 3.1rem;
        border: none;
    }
}