.header{
    max-width: 1440px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 40px;
    padding-bottom: 46px;
    padding-left: 15px;
    padding-right: 15px;
    @media(max-width: 1350px){
        max-width: 1140px;
    }
    @media(max-width: 1200px){
        max-width: 960px;
        padding-bottom: 65px;
    }
    @media(max-width: 990px){
        max-width: 100%;
    }
    &-item{
      display: flex;
     justify-content: space-between;
     align-items: center;
    }  
    &__tel{
        text-decoration: none;
        color: $gray;
        font-weight: 500;
        transition:0.3s ease ;
        letter-spacing: 0.075rem;
        &:hover{
         color: $orange;   
        }
        @media(max-width: 990px){
            text-indent: -9999px;
            width: 2.5rem;
            height: 2.5rem;
            background-color: transparent;
            border: 1px solid $gray;
            background-image: url(../../../img/tel.svg);
            background-repeat: no-repeat;
            border-radius: 100%;
            background-size: 1rem 0.87rem;
            background-position:center center ;
        }
    }
    &__menu{
        @media(max-width:990px){
            display: block;
            text-indent: -9999px;
            width: 2.5rem;
            height: 2.5rem;
            background-color: transparent;
            border: 1px solid $gray;
            background-image: url(../../../img/menu.svg);
            background-repeat: no-repeat;
            border-radius: 100%;
            background-size: 1rem 0.87rem;
            background-position:center center;
            margin-left: 20px;
        }
        @media(min-width:991px){
            display: none;
        }
    }
}
.header-cart{
    position: relative;
    width: 2.5rem;
    height: 2.5rem;
    margin-left: 80px;
    @media(max-width: 1350px){
        margin-left: 40px;
    }
    @media(max-width: 1200px){
        margin-left: 20px;
    }
    &__button{
        width: 2.5rem;
        height: 2.5rem;
        background-color: $orange;
        border: none;
        border-radius: 100%;
        background-image: url("data:img/png;base64,");
        background-size: 1rem 0.87rem;
        background-repeat: no-repeat;
        background-position-x: center;
        background-position-y: center;
        @media(max-width: 990px){
            background-color: transparent;
            border: 1px solid $gray;
            background-image: url(../../../img/cart-black.svg);
        }
    }
    &__count{
        position: absolute;
        top: 6px;
        right: 9px;
        font-size: 0.5rem;
        color: $white;
        @media(max-width: 990px){
            color: $gray;
        }
    }
}
.logo{
    display: block;
    background-repeat: no-repeat;
    background-image: url(../../../img/logo.svg);
    width: 120px;
    height: 32px;
    margin-right: 120px;
    background-size: contain;
    @media(max-width: 1350px){
        margin-right: 50px;
    }
    @media(max-width: 1200px){
        margin-right: 32px;
    }
}
nav{
    @media(max-width: 990px){
        display: none;
    }
}
.nav{
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    &-item{
    margin-right: 48px;
    @media(max-width: 1350px){
        margin-right: 30px;
    }
    @media(max-width: 1200px){
        margin-right: 22px;
    }
    }
    &-link{
        text-decoration:none ;
        color: $gray;
        transition:0.3s ease ;
        &:hover{
         color: $orange;   
        }
    }
}
